import { createContext, useEffect, useMemo, useState } from "react";
import { Accordion, AccordionDetails, AccordionSummary, Alert, AlertTitle, Card, CardContent, CardHeader, Container, CssBaseline, Divider, Grid, Input, Stack, Typography } from "@mui/material";

import { Game, loadGames } from "./Api";
import { useLocalStorage } from "./Hooks";
import { Box } from "@mui/system";

const TEAM_ID = "023MMCAFRO000000VS548985VTNSAQK7";

interface MatchProps {
    game: Game;
}

function Match(props: MatchProps) {
    return (
        <Container>
            <Typography color="text.secondary">
                <span>{props.game.dateYMD}</span>
                {props.game.time ? (
                    <> - <span>{props.game.time} Uhr</span></>
                ) : <></>}
            </Typography>
            {props.game.guestTeam ? (
                <Typography>
                    {props.game.homeTeam} - {props.game.guestTeam}
                </Typography>
            ) : (
                <Typography>
                    SPIELFREI
                </Typography>
            )}
        </Container>
    );
}

interface MatchListProps {
    games: Game[];
}

function MatchList(props: MatchListProps) {
    return (
        <Stack spacing={2} divider={<Divider />}>
            {props.games.map(game => <Match key={game.id} game={game} />)}
        </Stack>
    );
}

interface CheckResultProps {
    matchOnDay?: Game;
}

function CheckResult(props: CheckResultProps) {
    const determineMessageYes = (matchOnDay: Game) => `Ja, ${matchOnDay.homeTeam} gegen ${matchOnDay.guestTeam}.`;
    const determineMessageNo = () => "Nein.";
    const determineMessage = (matchOnDay?: Game) => matchOnDay ? determineMessageYes(matchOnDay) : determineMessageNo();

    return (
        <Alert severity={props.matchOnDay ? "success" : "error"}>
            <AlertTitle>{determineMessage(props.matchOnDay)}</AlertTitle>
        </Alert>
    );
}

function isGameCanceled(game: Game): boolean {
    return game.guestTeam === undefined || game.guestTeam === null;
}

function findMatchOnDay(games: Game[], date: string): Game | null {
    for (const game of games) {
        if (game.dateYMD === date) {
            return game;
        }
    }
    return null;
}

interface DateCheckProps { }

function DateCheck(props: DateCheckProps) {
    const [games, setGames] = useLocalStorage("GAMES", []);
    const [matchOnDay, setMatchOnDay] = useState(null);
    const [date, setDate] = useState(new Date().toISOString().substring(0, 10));

    useMemo(async () => {
        const teamId = TEAM_ID;
        const games = await loadGames(teamId);
        setGames(games);
    }, []);

    useMemo(async () => {
        const game = findMatchOnDay(games, date);
        if (game === null || isGameCanceled(game)) {
            setMatchOnDay(null);
        } else {
            setMatchOnDay(game);
        }
    }, [games, date]);

    return (
        <Box sx={{ py: 10, px: 5 }}>
            <Stack spacing={3}>
                <Typography variant="h2" sx={{ textAlign: "center" }}>
                    Spiele ich am
                </Typography>
                <Input type="date" value={date} onChange={e => setDate(e.target.value)} />
            </Stack>
            <Box sx={{ mt: 10 }}>
                <CheckResult matchOnDay={matchOnDay} />
            </Box>
        </Box>
    );
}

export default function App() {
    const [games, setGames] = useLocalStorage("GAMES", []);

    return (
        <CssBaseline>
            <Container>
                <DateCheck />

                <Accordion>
                    <AccordionSummary>
                        Spielplan
                    </AccordionSummary>
                    <AccordionDetails>
                        <MatchList games={games} />
                    </AccordionDetails>
                </Accordion>
            </Container>
        </CssBaseline>
    );
}