const HOST = "https://www.fussball.de";
const CORS_PROXY = "https://api.allorigins.win/get?url=";

export interface Game {
    id: string;
    homeTeam: string;
    guestTeam?: string;
    dateYMD: string;
    time?: string;
}

function lastItem(collectionWithLength): any {
    return collectionWithLength[collectionWithLength.length - 1];
}

function parseHtmlTable(html: string): Game[] {
    const cleanedHtml = html.replace(/&nbsp;/g, "");
    const parser = new DOMParser();
    const table = parser.parseFromString(cleanedHtml, "text/xml");
    const tableBody = table.getElementsByTagName("tbody")[0];

    const games = [];
    const extractDateTimeRegex = /(\d{2}\.\d{2}\.\d{4})\s+-\s+(\d{2}:\d{2})?/;

    for (let i = 0; i < tableBody.children.length; i += 3) {
        const headline = tableBody.children[i];
        const headlineContent = headline.textContent.trim();
        const dateTimeMatch = extractDateTimeRegex.exec(headlineContent);
        const [dateDMY, time] = [dateTimeMatch[1], dateTimeMatch[2]];

        const [day, month, year] = dateDMY.split(".");
        const dateYMD = `${year}-${month}-${day}`;

        const competition = tableBody.children[i + 1];
        const competitionCell = lastItem(competition.getElementsByTagName("td"));
        const gameId = competitionCell.textContent.trim();

        const teams = tableBody.children[i + 2];
        const [homeTeamRaw, guestTeamRaw] = teams.getElementsByClassName("club-name");
        const [homeTeam, guestTeam] = [homeTeamRaw.textContent.trim(), guestTeamRaw?.textContent.trim()];

        games.push({
            gameId,
            homeTeam,
            guestTeam,
            dateYMD,
            time,
        });
    }

    return games;
};

function routeUrlThroughCorsProxy(url: string): string {
    return `${CORS_PROXY}${encodeURIComponent(url)}`;
}

export function loadGames(teamId: string): Promise<Game[]> {
    const url = `${HOST}/ajax.team.matchplan/-/mime-type/JSON/mode/PAGE/prev-season-allowed/false/show-filter/false/team-id/${teamId}/max/60/datum-von/2021-08-05/datum-bis/2022-06-30/offset/0`;
    const urlWithCors = routeUrlThroughCorsProxy(url);

    return fetch(urlWithCors)
        .then(res => res.json())
        .then(res => JSON.parse(res["contents"]))
        .then(res => parseHtmlTable(res.html));
}